import React, { Component } from 'react';
import dataBase from "../../store/ppData.js";
import Checkbox from "../Checkbox";
import CheckboxOtkl from "../CheckboxOtkl";
import RadioList from "../RadioList";
import Textarea from "../Textarea";
import cicleFn from "../../fn/cicleFn";
import "./Pp.css";
import KritikaManager from "../KritikaManager";

export default class PpManager extends Component {
  
  render() {   

    // Ð¿Ð¾ÐºÐ°Ð· ÑÐ¿Ð¸ÑÐºÐ° Ð±ÑÐµÐ´
    let bredNameArr = cicleFn(dataBase, "bred"); 

    let bredList = bredNameArr.map(item => (
      <div key={item} className="list__item">
        <Checkbox 
        checked = {this.props.psystatus[item].isChecked}
        onChange = {this.props.toggleSymptom}
        name = {item}
        label = {dataBase[item].label}
      />
      </div>
    ));

    let bredListShow = () => {
      if(this.props.psystatus["bredOtkl"].isChecked) {
        return "";
      } else {
        return bredList;
      }
    }

    // Ð¿Ð¾ÐºÐ°Ð· ÑÐ¿Ð¸ÑÐºÐ° Ð³Ð°Ð»Ð»ÑÑÐ¸Ð½Ð°ÑÐ¸Ð¸
    let gallNameArr = cicleFn(dataBase, "gall"); 

    let gallList = gallNameArr.map(item => (
      <div key={item} className="list__item">
        <Checkbox 
        checked = {this.props.psystatus[item].isChecked}
        onChange = {this.props.toggleSymptom}
        name = {item}
        label = {dataBase[item].label}
      />
      </div>
    ));

    let gallListShow = () => {
      if(this.props.psystatus["gallOtkl"].isChecked) {
        return "";
      } else {
        return gallList;
      }
    }
    
    return <div>
      <div className="manager">
        <div className="manager__col manager__col--1">

          <KritikaManager
            toggleRadio={this.props.toggleRadio} 
            toggleText={this.props.toggleText}
            psystatus={this.props.psystatus}
          />  

          <div className="list__title">
            <span className="list__title-lable">ÐÐ¿Ð°ÑÐ½ÑÐµ ÑÐµÐ½Ð´ÐµÐ½ÑÐ¸Ð¸:</span>
            <CheckboxOtkl 
              name = "opasnyeOtkl"
              onChange = {this.props.toggleSymptom}
              psystatus = {this.props.psystatus}
              dataBase = {dataBase}
            />
          </div>
          <RadioList 
            name="opasnye" 
            dataBase={dataBase} 
            onChange={this.props.toggleRadio} 
            psystatus={this.props.psystatus}
          />

          <div className="list__title">ÐÐ³ÑÐµÑÑÐ¸Ñ:</div>
          <RadioList 
            name="opasnyeAgressia" 
            dataBase={dataBase} 
            onChange={this.props.toggleRadio} 
            psystatus={this.props.psystatus}
          />

          <Textarea
            onChange={this.props.toggleText}
            name="opasnyeText"
            label="ÐÐ¿Ð¸ÑÐ°Ð½Ð¸Ðµ Ð¾Ð¿Ð°ÑÐ½ÑÑ ÑÐµÐ½Ð´ÐµÐ½ÑÐ¸Ð¹"
            value={this.props.psystatus.gallText}
          />
        
        </div>
        <div className="manager__col manager__col--1">

          {/*ÑÐ¸ÐºÐ» "bred"*/}
          <div className="list__title">
            <span className="list__title-lable">ÐÑÐµÐ´:</span>
            <CheckboxOtkl 
              name = "bredOtkl"
              onChange = {this.props.toggleSymptom}
              psystatus = {this.props.psystatus}
              dataBase = {dataBase}
            />
          </div>
          {bredListShow()}

          <Textarea
            onChange={this.props.toggleText}
            name="bredText"
            label="ÐÐ¿Ð¸ÑÐ°Ð½Ð¸Ðµ Ð±ÑÐµÐ´Ð°"
            value={this.props.psystatus.bredText}
          />
       
          {/*ÑÐ¸ÐºÐ» "gall"*/}
          <div className="list__title">        
            <span className="list__title-lable">ÐÐ°Ð»Ð»ÑÑÐ¸Ð½Ð°ÑÐ¸Ð¸:</span>
            <CheckboxOtkl 
              name = "gallOtkl"
              onChange = {this.props.toggleSymptom}
              psystatus = {this.props.psystatus}
              dataBase = {dataBase}
            />
          </div>
          {gallListShow()}

          <Textarea
            onChange={this.props.toggleText}
            name="gallText"
            label="ÐÐ¿Ð¸ÑÐ°Ð½Ð¸Ðµ Ð³Ð°Ð»Ð»ÑÑÐ¸Ð½Ð°ÑÐ¸Ð¹"
            value={this.props.psystatus.gallText}
          />
        
        </div>
      </div>

    </div>;
  }
}


